var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'2. Antecedentes de los ambientes de aprendizaje'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_c('p',{staticClass:"mb-4"},[_vm._v("Para conocer sobre esta temática, lo invitamos a ver el siguiente video.")]),_vm._m(0),_vm._m(1),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(2),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Aulas tradicionales Parte 1"}},[_c('p',[_vm._v("Durante siglos, la educación formal se centró en aulas con maestros impartiendo lecciones directamente a los estudiantes. Este modelo ha sido la base de la educación desde la antigüedad, hasta la era moderna. Las aulas tradicionales se caracterizan por la enseñanza directa y la interacción cara a cara entre maestros y estudiantes. Desde aproximadamente el 28 a. C. hasta el año 2000, la enseñanza se basaba predominantemente en un modelo conductista. Este modelo, influenciado por psicólogos como B.F. Skinner y Ivan Pavlov, se centraba en la idea de que el aprendizaje humano podía ser observado y medido a través de cambios en el comportamiento. Según este enfoque, el docente era el principal actor en el proceso de enseñanza-aprendizaje, desempeñando un papel central en la transmisión de conocimientos y controlando el entorno educativo.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Aulas tradicionales Parte 2"}},[_c('p',[_vm._v("En este contexto, el docente era la figura de autoridad y el principal transmisor de la información. La enseñanza se realizaba mediante la presentación de contenidos y la realización de ejercicios repetitivos. No existían recursos didácticos avanzados como los que conocemos hoy en día, lo que limitaba la diversidad de métodos de enseñanza. El trabajo individual era la norma, y los estudiantes rara vez colaboraban entre sí en actividades de aprendizaje (Alarcón, s/f).")]),_c('p',[_vm._v("La Edad Antigua, es un período histórico que se caracteriza por el desarrollo de la escritura, una de las innovaciones más significativas de la humanidad. La invención de la escritura permitió la transmisión y conservación de conocimientos, mensajes e información a lo largo del tiempo y el espacio.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Aprendizaje por correspondencia"}},[_c('p',[_vm._v("Con la escritura, surgió la posibilidad de la comunicación asincrónica, es decir, la capacidad de enviar y recibir mensajes sin necesidad de la presencia simultánea del emisor y el receptor. Las cartas se convirtieron en un medio esencial para el intercambio de información. Esta forma de comunicación facilitó el aprendizaje y la difusión de conocimientos, permitiendo a las personas compartir ideas, instrucciones y relatos, a largas distancias.")]),_c('p',[_vm._v("Uno de los primeros ejemplos de aprendizaje a distancia, puede encontrarse en el uso de cartas para enseñar y aprender. Los filósofos y maestros de la antigüedad, como Aristóteles, utilizaban la correspondencia para comunicarse con sus discípulos y transmitirles enseñanzas. Esta modalidad permitió que el conocimiento llegara a personas que no podían estar físicamente presentes en los centros de aprendizaje. ")]),_c('p',[_vm._v("El aprendizaje por correspondencia en la Edad Antigua, es un precursor de los modernos ambientes de aprendizaje a distancia. Aunque rudimentarios en comparación con las tecnologías actuales, estos primeros esfuerzos demostraron el potencial de la educación fuera del aula física. La escritura permitió que los conocimientos se documentaran y se transmitieran a través de generaciones, creando una base para el desarrollo de sistemas educativos más complejos (Alarcón, s/f).")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(3),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Falta de aprendizaje colaborativo Parte 1"}},[_c('p',[_vm._v("Durante el período comprendido entre 1801 y 1900, el enfoque educativo se centró en atender a minorías con necesidades específicas. Este modelo, influenciado por el conductismo, se caracterizaba por un enfoque en el control y la modificación del comportamiento observable. Los estudiantes adoptaban un papel pasivo en el proceso de enseñanza-aprendizaje, siendo receptores de información, más que participantes activos. Este modelo carecía de comunicación bidireccional, lo que significa que no había un intercambio activo de ideas y conocimientos entre el docente y los estudiantes. El docente dictaba el contenido, y los estudiantes simplemente lo recibían sin tener la oportunidad de expresar sus opiniones, dudas, o contribuir de manera significativa al proceso de aprendizaje (portal LaHistoria, 2024).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Falta de aprendizaje colaborativo Parte 2"}},[_c('p',[_vm._v("La educación durante este período, no fomentaba el aprendizaje colaborativo. Los estudiantes trabajaban de manera individual, completando tareas y ejercicios de manera solitaria. Este enfoque limita el desarrollo de habilidades sociales y de trabajo en equipo, debido a que no había oportunidades para la colaboración y la interacción entre compañeros.")]),_c('p',[_vm._v("Aunque el enfoque principal era la enseñanza individualizada y pasiva, algunas iniciativas comenzaron a centrarse en atender a minorías con necesidades educativas especiales. Sin embargo, estos esfuerzos eran limitados y no estaban ampliamente implementados en los sistemas educativos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Mayor interacción y medios de comunicación"}},[_c('p',[_vm._v("Entre aproximadamente 1901 y 1970, los ambientes de aprendizaje experimentaron una notable evolución, marcada por varios cambios en la interacción entre docentes y alumnos, así como en las metodologías y herramientas utilizadas. Durante este período, la comunicación entre docentes y alumnos se vio facilitada por la disponibilidad de medios como el correo y el teléfono. Estas herramientas permitieron una comunicación más frecuente y efectiva, lo que contribuyó a fortalecer la relación educativa y a personalizar el apoyo a los estudiantes. El uso del correo permitió a los estudiantes enviar sus trabajos y recibir retroalimentación de sus tutores, mientras que el teléfono facilitó consultas rápidas y la resolución de dudas (Alarcón, s/f).")]),_c('p',[_vm._v("El papel de los tutores se volvió más prominente, ofreciendo un apoyo más personalizado y constante a los estudiantes. Este enfoque promueve la autoevaluación, donde los estudiantes se involucraron de manera activa en la valoración de su propio progreso y desempeño. La autoevaluación fomenta una mayor responsabilidad y autonomía en el proceso de aprendizaje, permitiendo a los estudiantes identificar sus fortalezas y áreas de mejora.")])])])],1)]),_vm._m(4),_c('TabsC',{staticClass:"color-primario mb-5"},[_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"Aulas presenciales","icon":require('@/assets/bullets/2.svg')}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("Aulas presenciales")]),_c('p',[_vm._v("Las aulas seguían siendo el entorno principal de aprendizaje, pero con una mayor interacción y participación activa de los estudiantes. Los docentes comenzaron a implementar métodos más dinámicos, como discusiones en grupo y actividades prácticas.")])]),_c('div',{staticClass:"col-md-8 col-lg-5 col-xl-4 order-1 mb-3 mb-lg-0"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/4.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titul